import React, { createContext, useState, useEffect, Suspense, lazy } from 'react';
import { HashRouter as Router, Routes, Route } from 'react-router-dom';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import { CircularProgress, Box, CssBaseline } from '@mui/material';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import io from 'socket.io-client';

// Lazy load components
const VoiceChat = lazy(() => import('./components/VoiceChat'));
const About = lazy(() => import('./pages/About'));
const Contact = lazy(() => import('./pages/Contact'));
const Support = lazy(() => import('./pages/Support'));
const Privacy = lazy(() => import('./pages/Privacy'));
const Terms = lazy(() => import('./pages/Terms'));
const Guidelines = lazy(() => import('./pages/Guidelines'));
const NavBar = lazy(() => import('./components/NavBar'));
const Footer = lazy(() => import('./components/Footer'));

// Create context with default value
export const SocketContext = createContext(null);

const theme = createTheme({
  palette: {
    mode: 'dark',
    primary: {
      main: '#6C63FF',
    },
    secondary: {
      main: '#50C878',
    },
    background: {
      default: '#1a1a2e',
      paper: '#242444',
    },
  },
  typography: {
    fontFamily: "'Poppins', sans-serif",
    button: {
      textTransform: 'none',
    },
  },
  components: {
    MuiButton: {
      styleOverrides: {
        root: {
          borderRadius: 8,
        },
      },
    },
    MuiPaper: {
      styleOverrides: {
        root: {
          backgroundImage: 'none',
        },
      },
    },
  },
});

// Loading component
const LoadingFallback = () => (
  <Box 
    sx={{ 
      display: 'flex', 
      justifyContent: 'center', 
      alignItems: 'center', 
      height: '100vh',
      background: theme.palette.background.default
    }}
  >
    <CircularProgress />
  </Box>
);

const App = () => {
  const [socket, setSocket] = useState(null);
  const [isConnecting, setIsConnecting] = useState(true);

  useEffect(() => {
    let newSocket;
    try {
      const BACKEND_URL = process.env.REACT_APP_BACKEND_URL || 'https://airspeak.onrender.com';
      newSocket = io(BACKEND_URL, {
        transports: ['polling', 'websocket'],
        secure: true,
        rejectUnauthorized: false,
        withCredentials: true,
        autoConnect: true,
        reconnection: true,
        reconnectionAttempts: 10,
        reconnectionDelay: 1000
      });

      newSocket.on('connect', () => {
        console.log('Connected to server with ID:', newSocket.id);
        setIsConnecting(false);
        newSocket.emit('join', {});
      });

      newSocket.on('connect_error', (error) => {
        console.error('Socket connection error:', error);
        console.error('Connection details:', {
          url: BACKEND_URL,
          transport: newSocket.io.engine.transport.name
        });
        setIsConnecting(false);
      });

      setSocket(newSocket);
    } catch (error) {
      console.error('Socket initialization error:', error);
      setIsConnecting(false);
    }

    return () => {
      console.log('Cleaning up socket connection');
      if (newSocket) {
        newSocket.close();
      }
    };
  }, []);

  if (isConnecting) {
    return <LoadingFallback />;
  }

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <SocketContext.Provider value={socket}>
        <Router>
          <Suspense fallback={<LoadingFallback />}>
            <div className="App">
              <NavBar />
              <main style={{ 
                minHeight: 'calc(100vh - 64px - 300px)', 
                padding: '24px 0',
                display: 'flex',
                flexDirection: 'column'
              }}>
                <Routes>
                  <Route path="/" element={<VoiceChat socket={socket} />} />
                  <Route path="/about" element={<About />} />
                  <Route path="/contact" element={<Contact />} />
                  <Route path="/support" element={<Support />} />
                  <Route path="/privacy" element={<Privacy />} />
                  <Route path="/terms" element={<Terms />} />
                  <Route path="/guidelines" element={<Guidelines />} />
                </Routes>
              </main>
              <Footer />
            </div>
          </Suspense>
        </Router>
      </SocketContext.Provider>
      <ToastContainer
        position="bottom-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
      />
    </ThemeProvider>
  );
};

export default App;
